.tag-box {
  @apply text-indigo font-medium text-px14 leading-px18 tracking-px05;
  @apply px-5 py-2;
  @apply bg-violet-light;
  @apply cursor-pointer;
  @apply rounded-full;
  @apply transition-colors;

  &[hidden] {
    display: none;
  }

  &:hover {
    @apply bg-melrose;
  }

  &.ellipsis {
    @apply bg-violet-tint;
    @apply text-white;
    &:hover {
      @apply bg-violet;
    }
  }

  .is-gi & {
    @apply bg-gray-light text-teal-dark;

    &.ellipsis {
      @apply bg-blue_green text-white;
    }
  }
}
